import React from 'react'
import {FaGithub, FaTwitter, FaLinkedinIn} from 'react-icons/fa'


export default function Footer() {
    const GITHUB_URL = "https://github.com/MartineauRemi"
    const LINKEDIN_URL = "https://www.linkedin.com/in/rémi-martineau"
    const TWITTER_URL = "https://twitter.com/MartineauRmi1"

    return (
        <footer id="footer" className="footer">
            <ul className="footer__social-medias">
                <li><a href={GITHUB_URL} target="_blank" rel="noreferrer"><FaGithub /></a></li>
                <li><a href={LINKEDIN_URL} target="_blank" rel="noreferrer"><FaLinkedinIn /></a></li>
                <li><a href={TWITTER_URL} target="_blank" rel="noreferrer"><FaTwitter /></a></li>
            </ul>
            <ul className="footer__links">
                <li><a href="#hero">Accueil</a></li>
                <li><a href="#about">À Propos</a></li>
                <li><a href="#projects">Portfolio</a></li>
                <li><a href="#footer">Contact</a></li>
            </ul>
            <div className="footer__copyrights">
                <div><a href="#hero">Created and Designed By Rémi Martineau © 2021.</a></div>
                <div>Icons made by <a href="https://www.freepik.com" title="Freepik">Freepik</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a></div>
            </div>
        </footer>
    )
}
